/* Global styles */
:root {
    --color_primary: #03BBDA;
    --color_secundary: #8FEBF5;
    --color_red: rgba(225, 48, 51, 0.705);
    --color_background: #F4F2F2;
}
body{
    overflow-x: hidden;
    font-family: 'Major Mono Display', monospace !important;
}
::selection {
    background: rgba(225, 48, 51, 0.705);
    background: var(--color_red);
    color: white;
    text-shadow: 1px 1px white;
}
.flex{
    display: flex;
}
.center-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
#App{
    background-color: #F4F2F2;
    background-color: var(--color_background);
}

#content{
    min-height: 70vh;
}
footer{
    min-height: 20vh;
}
header{
    min-height: 10vh;
}
/* HOME */
.Home__Link{
    text-decoration: none;
    color: black;
}
.Home__Link:hover .btn-Home{
    display: block !important;
} 
.Home__Number{
    font-family: 'Codystar', cursive;
    font-size: 3.5rem;
}
.Home__Item{
    cursor: pointer;
}
.Home__Title{
    font-family: 'Bebas Neue', cursive;
    font-size: 4.5rem;
    margin: 0 0 !important;
    pointer-events: fill;
}
.Home__Line {
    height: 1px;
    background-color: black;
    margin-top: 0;
}
.Home__Link:hover{
    text-decoration: none !important;
    color: rgba(225, 48, 51, 0.705) !important;
    color: var(--color_red) !important;
}
.Home__Link:hover .Home__Number{
    background-color: black;
    color: white;
}
/* Not found */
.NotFound__Content{
    text-align: right;
}
.NotFound__Messaje{
    font-family: 'Major Mono Display', monospace;
    font-size: 7rem;
}
.NotFound__Number{
    font-family: 'Codystar', cursive;
    font-size: 10rem;
    /* background-color: black;
    color: white; */
    padding: 10px;
}
/* Header */

.Top__Title{
    font-family: 'Bebas Neue', cursive;
    font-size: 1.5rem !important;
    color: black;
    text-align: right;
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
.Top__Title:hover{
    text-decoration: none;
    color: rgba(225, 48, 51, 0.705);
    color: var(--color_red);
}
.Top__Menu:hover{
    text-decoration: none;
    color: rgba(225, 48, 51, 0.705);
    color: var(--color_red);
}
.Top__Menu{
    font-family: 'Bebas Neue', cursive;
    font-size: 1rem;
    color: black;
    text-align: right;
    padding: 0px 0px 25px 25px;
    font-weight: 900;
}
.Top__Menu ul {
    display: none;
    list-style: none;
    margin: 2% 0 0px -4%;
    text-align: right;
    position: absolute;
}
  
.Top__Menu:hover ul{
    display: block;
}
  
.Top__Menu li {
    margin: 10px 0px;
}
.Top__Menu li a {
    color: black;
    text-decoration: none;
}
.Top__Menu li a:hover {
    color: rgba(225, 48, 51, 0.705);
    color: var(--color_red);
    text-decoration: none;
}


@-webkit-keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
}
@keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
}
  

/* Footer */

.Footer__Title{
    font-family: 'Major Mono Display', monospace;
    font-size: 3.5rem;
    color: white !important;
    text-align: right;
}
.Footer__Content{
    font-family: 'Major Mono Display', monospace;
    font-size: 1.5rem;
    color: white !important;
    text-align: left;
}
.Footer__Icons{
    font-size: 3.5rem;
    color: white !important;
    text-align: right;
    display: flex;
    align-content: center;
}
.Footer__Icons div {
    margin: 0 10px;
}
.Footer__Social-Item{    
    color: white;
}
.Footer__Social-Item:hover{    
    color: rgba(225, 48, 51, 0.705);    
    color: var(--color_red);
}
.Footer__Menu{
    list-style:none
}
.Footer__Menu-item{
    text-decoration: none;
    color: black;
}
.Footer__Menu-item:hover{
    text-decoration: none;
    color: rgba(225, 48, 51, 0.705);
    color: var(--color_red);
}

.btn__xl{
    font-family: 'Major Mono Display', monospace;
    background-color: black !important;
    color: white !important;
    font-size: 2.5rem !important;
}
.btn-Home{
    margin-top: -6rem;
    float: right !important;
    display: none !important;
}

/* About me */
.About__Title{
    font-family: 'Bebas Neue', cursive;
    font-size: 3rem;
}
.About__Content{
    font-family: 'Major Mono Display', monospace;
    font-size: 1.5rem;
    background-color: white;
    padding: 15px;
}
.About__Img-custom{
    position: absolute;
    z-index: -1;
    cursor: no-drop;
}
.About__Img-Text{
    background-color: black;
    color: white;
}
.About__Content-Contact{
    text-align: right;
}
.About__Contact a i{
    margin-left: 20px;
}
.About__Icon{
    font-family: 'Bebas Neue', cursive;
    font-size: 1.5rem;
    color: black;
    
}
.About__Icon:hover{
    text-decoration: none;
    color: white;
    background-color: black;
    padding: 0px 5px;
}
.About__Icon-Text{
    padding: 5px;
}
.About__Icon:hover .About__Icon-Text{
    color: white;
    background-color: black;
    padding: 5px;
}
.About__Img-custom{
    max-width: 30%;;
}

/* Contact */
.Contact__title{
    font-family: 'Bebas Neue', cursive;
    font-size: 10rem;
    font-weight: 300;
}
.Contact__body{
    font-size: 2.5rem;
    font-weight: 300;
}
/* Sidebar */

#sidebar-wrapper {
    min-height: 100vh;
    text-align: center;
    transition: margin .25s ease-out;
}
  
#sidebar-wrapper .sidebar-heading {
    padding: 0.875rem 1.25rem;
    font-size: 1.2rem;
}
  
#sidebar-wrapper .list-group {
    width: 15rem;
}
  
#page-content-wrapper {
    min-width: 100vw;
}
  
#wrapper.toggled #sidebar-wrapper {
    margin-left: 0;
}

